@import url(https://fonts.googleapis.com/css?family=Open+Sans|Pacifico);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
}

p {
  margin: 0;
  display: inline-block;
}

.chatroom {
  width: 360px;
  height: 100%;
  background-color: rgba(0,0,0, .7);
  display: inline-block;
}

.chatroom h3 {
  font-family: 'Pacifico', cursive;
  background-color: rgba(30, 228, 148, 0.7);
  color: #FFF;
  margin: 0;
  height: 40px;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.chatroom .chats {
  box-sizing: border-box;
  padding: 0 20px;
  margin: 0;
  height: 100%;
  overflow-y: scroll;
}

.chatroom .chats .chat {
  background: rgba(255, 255, 255, 0.8);
  position: relative;
  padding: 5px 13px;
  font-size: 14px;
  border-radius: 10px;
  list-style: none;
  float: left;
  clear: both;
  margin: 10px 0;
  max-width: 200px;
}

.chatroom .chats .chat img {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  position: absolute;
  top: -10px;
  left: 0px;
}

.chatroom .chats .chat.right {
  float: right;
  clear: both;
}

.chatroom .chats .chat a {
  text-decoration: none;
  color:#3498DB;
}

.chatroom .input {
  position: absolute;
  bottom: 0;
  height: 40px;
  /* width: 100%; */
  background: rgba(30, 228, 148, 0.7);
  display: flex;
  flex-direction: row;
  align-items: center;
}

.chatroom .input input[type="text"] {
  background: rgba(255, 255, 255, 0.5);
  outline: 0;
  border: 2px solid #1EE494;
  padding: 0 5px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  margin-left: 10px;
  height: 30px;
  width: 270px;
  box-sizing: border-box;
  font-size: 14px;
}

.chatroom .input input[type="submit"] {
  background: transparent;
  border: 1px solid #1EE494;
  height: 30px;
  width: 70px;
  font-size: 14px;
  box-sizing: border-box;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.chatroom .input input[type="submit"]:hover {
  background: #1EE494;
  color: #FFF;
}
[draggable] {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
}

.Card {
  height: 100%;
  width: 100%;
  margin: 5px;
  margin-bottom: 0;
  cursor: move;
}

.Card.top, .Card.bottom  {
  display: inline;
}

.Card .role-card {
  height: 140px;
  width: 100px;
  display: inline-block;
  vertical-align: middle;
  border: 4px solid white;
}

.role-card.placeholder, .over {
  border: 4px dotted white;
  cursor: auto;
}
.GameBoard {
  position: relative;
  display: flex;
  height: 100%;
  width: 55%;
  vertical-align: top;
}

.board-top {
  display: inline-block;
  position: absolute;
  top: 5px;
  left: 20%;
}

.board-left {
  display: inline;
  position: absolute;
  top: 15%;
  left: 0;
}

.board-right {
  display: inline;
  position: absolute;
  top: 15%;
  right: 0;
}

.board-bottom {
  display: inline;
  position: absolute;
  left: 20%;
  bottom: 5px;
}

.board-middle {

}
.InventoryScroll {
  height: 100%;
  width: 20%;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: green;
  display: grid;
  grid-template-columns: 150px 150px;
  grid-gap: 5px;
}

.Card.side {
  height: auto;
}
.App {
  background-color: grey;
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: stretch;
}
